<template>
  <div>
    <!-- 导航 -->
    <van-nav-bar class="page-nav-bar"
                 title="登录">
      <van-icon slot="left"
                name="cross"
                @click="$router.push($route.query.redirect || '/my')"></van-icon>
    </van-nav-bar>
    <!-- 表单 -->
    <van-form @submit="onSubmit"
              ref="formRef">
      <van-field name="mobile"
                 placeholder="请输入手机号"
                 v-model="user.mobile"
                 :rules="formRules.mobile"
                 type="number"
                 maxlength="11">
        <i slot="left-icon"
           class="iconfont iconshouji"></i>
      </van-field>
      <van-field name="code"
                 placeholder="请输入验证码"
                 v-model="user.code"
                 :rules="formRules.code"
                 type="number"
                 maxlength="6">
        <i slot="left-icon"
           class="iconyanzhengma iconfont"></i>
        <template #button>
          <van-count-down :time="1000*60"
                          format="ss"
                          @finish='isShow=false'
                          v-if="isShow" />
          <van-button round
                      v-else
                      size="small"
                      type="default"
                      class="btn-small"
                      @click="sendNumBtn"
                      native-type="button">发送验证码</van-button>
        </template>
      </van-field>
      <div class="login-box-btn">
        <van-button block
                    type="info"
                    native-type="submit"
                    class="login-btn">
          提交
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { login, sendCode } from '@/api/user'
export default {
  data () {
    return {
      user: {
        mobile: '',
        code: ''
      },
      formRules: {
        mobile: [
          { required: true, message: '请输入手机号' },
          { pattern: /^1[3|5|6|7|8|9]\d{9}$/, message: '请输入正确的手机格式' }
        ],
        code: [
          { required: true, message: '请输入验证码' },
          { pattern: /^\d{6}$/, message: '验证码为6为数字' }
        ]
      },
      isShow: false
    }
  },
  methods: {
    async onSubmit () {
      this.$toast.loading({
        message: '登录中...',
        forbidClick: true, // 禁止背景点击
        duration: 0// 展示时长(ms)，值为 0 时，toast 不会消失
      })

      try {
        const { data: res } = await login(this.user)
        // console.log(res)
        this.$toast.success('登陆成功')
        this.$store.commit('saveToken', res.data)
        // 在成功登录后移除缓存
        this.$store.commit('removeCache', 'LayoutIndex')
        this.$router.push(this.$route.query.redirect || '/my')
      } catch (err) {
        if (err.response.status === 400) {
          this.$toast.fail('手机号或验证码错误')
        } else if (err.response.status === 507) {
          // console.log('服务器数据异常')
          this.$toast.fail('服务器数据异常')
        } else {
          // console.log('登录失败，请稍后重试', err)
          this.$toast.fail('登录失败，请稍后重试')
        }
        // console.log(err.response)
      }
    },
    async sendNumBtn () {
      // 单独校验手机号
      try {
        await this.$refs.formRef.validate('mobile')
      } catch (err) {
        return console.log(err)
      }
      // 验证通过，显示倒计时
      this.isShow = true
      // 发送请求
      try {
        await sendCode(this.user.mobile)
        this.$toast('发送成功')
      } catch (err) {
        if (err.response.status === 429) {
          this.$toast('请求次数频繁，请稍后重试')
        } else {
          this.$toast('发送失败，请稍后重试')
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.iconfont {
  font-size: 37px;
}
.btn-small {
  background-color: #ededed;
  font-size: 22px;
  width: 152px;
  height: 46px;
  line-height: 46px;
  color: #666;
}
.login-box-btn {
  padding: 53px 33px;
  .login-btn {
    background-color: #6db4fb;
    border: none;
  }
}
.van-field {
  font-size: 26px;
}
</style>
